<template>
    <div class="all_com">
        <div class="title_com">提现管理</div>
        <div class="exportList" style="margin:10px 0 0 20px;">
            <span>共{{this.totalNo}}个</span>
            <el-button size="mini" type="text" @click="exportList">导出</el-button>
        </div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>提现列表</span>
            </div>
            <div class="search_fun">
                <!-- <el-select v-model="status" class='small' style="width:100px" size='small' placeholder="请选择">
                    <el-option v-for="(item,index) in orderStatus" :key="index" :label="item" :value="index">
                    </el-option>
                </el-select> -->
                <el-select v-model="status" class='small' style="width:100px" size='small' placeholder="请选择">
                    <el-option v-for="(item,index) in withdrawStatus" :key="index" :label="withStatus[index]" :value="item">
                    </el-option>
                </el-select>
                <el-input v-model="keyword" class='search_input' size='small' placeholder="帐号或地址"></el-input>
                <el-button type="primary" size='small' @click='search'>搜索</el-button>
            </div>
            <el-table :data="tableData" stripe style="width: 100%;height:100%">
                <el-table-column prop="ID" label="Id" width="60">
                </el-table-column>
                <el-table-column prop="CreateTime" label="创建时间">
                    <template slot-scope="scope">
                        {{scope.row.CreateTime | date}}
                    </template>
                </el-table-column>
                <el-table-column prop="User.Account" label="账号">
                </el-table-column>
                <el-table-column prop="Item" label="类型">

                </el-table-column>
                <el-table-column prop="Amount" label="总额">

                </el-table-column>
                <el-table-column prop="Address" label="地址">
                    <template slot-scope="scope">
                        <!-- <a class="BTC_address" href="https://btc.com/" target="_Blank" style="color:#409EFF;">{{scope.row.Address}}</a> -->
                        <!-- <a v-if="scope.row.Item == 'ETH'" :href="'https://www.etherchain.org/account/'+scope.row.Address" target="_Blank" style="color:#409EFF;">{{scope.row.Address}}</a> -->
                        <a :href="'https://btc.com/'+scope.row.Address" target="_Blank" style="color:#409EFF;">{{scope.row.Address}}</a>
                    </template> 
                </el-table-column>
                <el-table-column prop="Status" label="状态">
                    <template slot-scope="scope">
                        {{withStatus[scope.row.Status]}}
                    </template>
                </el-table-column>
                <el-table-column prop="" label="审核">
                    <template slot-scope="scope">
                        <div v-if="scope.row.Status == 'processing'">
                            <el-button size="mini" @click="confirm(scope.row.ID)">通过</el-button>
                            <br>
                            <el-button style="margin-top:5px" size="mini" @click="cancel(scope.row.ID)">取消</el-button>
                        </div>
                        <div v-else-if="scope.row.Status == 'sending'">
                            <el-button size="mini" @click="cancel(scope.row.ID)">取消</el-button>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="AuditUser" label="审核人">

                </el-table-column>
                <el-table-column prop="AuditTime" label="审核时间">
                    <template slot-scope="scope">
                        <span v-if="scope.row.AuditTime">{{scope.row.AuditTime | date}}</span>
                        <span v-else></span>
                    </template>
                </el-table-column>
                <el-table-column prop="Tx" label="转账">

                </el-table-column>
                <el-table-column prop="TxTime" label="转账时间">
                    <template slot-scope="scope">
                        <span v-if="scope.row.TxTime">{{scope.row.TxTime | date}}</span>
                        <span v-else></span>
                    </template>
                </el-table-column>
                <!-- <el-table-column class='action' label="操作">
                    <template slot-scope="scope">
                        <el-button v-show='scope.row.Status == 0' @click='saleon(scope.row.ID)' type="primary" size="mini">
                            上架
                        </el-button>
                        <el-button @click='saleoff(scope.row.ID)' v-show='scope.row.Status == 1' type="primary" size="mini">
                            下架
                        </el-button>
                        <el-button v-show='scope.row.Status == 2' type="primary" size="mini">
                            结束预售
                        </el-button>
                        <el-button v-show='scope.row.Status == 4' type="primary" size="mini">
                            开启尾款
                        </el-button>

                        <el-button @click='Detail(scope.row.ID)' type="primary" size="mini">
                            详细
                        </el-button>

                    </template>
                </el-table-column> -->
            </el-table>
            <el-pagination class='page' :current-page.sync="pageNo" page-size='10' @current-change='getList' background layout="prev, pager, next" :total="totalNo">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    import { orderStatus,withStatus,sellType ,withdrawStatus} from "@/fetch/const";
    export default {
        name: "",
        data() {
            return {
                tableData: [],
                status: "",
                withdrawStatus: withdrawStatus,
                sellType: sellType,
                withStatus:withStatus,
                pageNo: 1,
                totalNo: 0,
                keyword:'',
                input: ""
            };
        },
        methods: {
            confirm(id){
               this.$confirm('是否确认通过?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$api('/admin/withdraw/withdraw_confirm', {
                    withdrawId:id,
                    notice:'通过成功'
                }).then(res => {
                    console.log(res)
                    this.getList()
                    // console.log(this.totalNo)
                });
                }).catch(() => {
                        this.$message({
                        type: 'info',
                        message: '取消操作'
                    });
                }); 
            },
            cancel(id){
               this.$confirm('是否取消(注意0时0分0秒请不要进行取消操作)?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$api('/admin/withdraw/withdraw_history_cancel', {
                    withdrawId:id,
                    notice:'已取消'
                }).then(res => {
                    console.log(res)
                    this.getList()
                    // console.log(this.totalNo)
                });
                }).catch(() => {
                        this.$message({
                        type: 'info',
                        message: '取消操作'
                    });
                }); 
            },
            exportList(){
                this.$confirm('是否确认导出?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$api('/admin/withdraw/export_withdraw_list', {
                    keyword:this.keyword,
                    status:this.status
                }).then(res => {
                    console.log(res)
                location.href = res.data.csvFilePath
                // console.log(this.totalNo)
            });
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '取消操作'
                });
            });
            },
            getList() {
                let params = {
                    status: this.status,
                    keyword:this.keyword,
                    pageNo: this.pageNo - 1,
                    pageSize: 10
                };
                this.$api("/admin/withdraw/list", params).then(res => {
                    this.tableData = res.data.list;
                    this.totalNo = res.data.page.totalNum;
                });
            },
            search() {
                this.pageNo = 1;
                this.getList();
            }
        },
        created() {
            this.getList();
        }
    };
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .detaila:hover {
        color: #409eff;
        text-decoration: underline;
    }
    .search_fun {
        height: 60px;
        position: relative;
        top: 10px;
        float: right;
    }
    .search_input {
        width: 200px;
    }
</style>


